@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-lg: 18px;
  --font-size-base: 16px;

  /* Colors */
  --color-white: #fff;
  --color-dimgray-100: #666;
  --color-dimgray-200: rgba(102, 102, 102, 0.25);
  --color-dimgray-300: rgba(102, 102, 102, 0.8);
  --color-gray: #111;
  --color-royalblue: #3870e0;
  --color-darkslategray: #333;

  /* Gaps */
  --gap-base: 16px;
  --gap-xs: 12px;
  --gap-5xs: 8px;
  --gap-9xs: 4px;

  /* Paddings */
  --padding-5xl: 24px;
  --padding-11xs: 2px;

  /* Border radiuses */
  --br-5xl: 24px;
  --br-base: 16px;
}
