.backgroundIcon,
.root {
  width: 100%;
  overflow: hidden;
}
.backgroundIcon {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  object-fit: cover;
}
.root {
  height: 1024px;
  position: relative;
  background-color: var(--color-white);
  line-height: normal;
  letter-spacing: normal;
}
