  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.profilePictureParent {
  flex-direction: row;
  gap: var(--gap-base);
}
.icons1,
.signUp {
  position: relative;
}
.icons1 {
  height: 24px;
  width: 24px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.signUp {
  font-size: var(--font-size-lg);
  font-family: var(--font-poppins);
  color: var(--color-white);
  text-align: center;
}
.buttonLabel {
  position: absolute;
  top: calc(50% - 14px);
  left: calc(50% - 86px);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.button {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-royalblue);
  align-self: stretch;
  height: 56px;
  position: relative;
  border-radius: var(--br-base);
  overflow: hidden;
  flex-shrink: 0;
}
.addressAndProfile,
.toCreateYour {
  margin: 0;
}
.termsOfService {
  text-decoration: underline;
  color: var(--color-gray);
}
.and {
  color: var(--color-dimgray-100);
}
.privacyPolicy {
  text-decoration: underline;
  color: var(--color-gray);
}
.span3 {
  color: var(--color-dimgray-100);
}
.moreAboutTermsOfServiceAn {
