}
.buttonParent,
.linkText,
.userCard {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.linkText {
  flex-direction: row;
  padding: 0 var(--padding-5xl) 0 0;
}
.buttonParent,
.userCard {
  flex-direction: column;
}
.buttonParent {
  align-self: stretch;
  gap: var(--gap-xs);
  font-size: var(--font-size-sm);
  color: var(--color-dimgray-100);
}
.userCard {
  position: absolute;
  top: 24px;
  left: 24px;
  gap: var(--gap-base);
  max-width: 100%;
}
.overlaySignIn,
.userProfile {
  position: absolute;
  top: 148px;
  left: 0;
  width: 100%;
  height: 247px;
  overflow: hidden;
  max-width: 100%;
}
.overlaySignIn {
  top: 313px;
  left: 474px;
  border-radius: var(--br-5xl);
  background-color: var(--color-white);
  width: 491px;
  height: 397px;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-darkslategray);
  font-family: var(--font-poppins);
}
